import "../App.css";
const Filters = () => {
  const filters = [
    {
      filter: "boy",
    },
    {
      filter: "girl",
    },
    {
      filter: "men",
    },
  ];
  return (
    <div className="filters">
      {filters.map((item) => (
        <button>{item.filter}</button>
      ))}
    </div>
  );
};
export default Filters;
